import React, { useEffect, useState } from "react";
import { Container, Button, Typography, makeStyles, Grid, Divider } from "@material-ui/core";
import moment from "moment"
import { Link } from "react-router-dom";
import api from "../api/api";
const useStyles = makeStyles((theme) => ({
  readMore: {
    position: "absolute", left: "50%", top: 620,
    [theme.breakpoints.down("xs")]: {
      position: "absolute", left: "50%", top: 200,
    },
  },
  readMore_Button: {
    color: "white", fontSize: 16, borderRadius: 20, position: "relative", left: "-50%",
    [theme.breakpoints.down("xs")]: {
      color: "white", fontSize: 13, borderRadius: 20, position: "relative", left: "-50%",
    },
  },

}));
const Home = (props) => {
  const classes = useStyles();
  const [news, setNews] = useState([])
  useEffect(() => {
    document.title = "MOM";
    fetchNews()
  }, []);

  const fetchNews = async () => {
    try {
      const response = await api.get(`/api/news`);
      return setNews(response.data.data);
    } catch (err) {
      return;
    }
  };

  const renderNews = () => {
    if (news.length) {
      return (<div>
        {news.map((a, i) => {
          return (<div key={i}>
            <Typography style={{ fontSize: 18, fontWeight: 600, marginTop: 20 }}>{a.title}</Typography>
            <Typography style={{ fontSize: 12, fontWeight: 500 }}>{moment(a.created).format("DD/MM/YYYY")}</Typography>
            <Typography variant="body1" style={{
              marginTop: 8, whiteSpace: "nowrap",
              textOverflow: "ellipsis",
              width: "100%",
              overflow: "hidden",
            }}>{a.body}</Typography>

            <hr color="white" style={{ marginTop: 15, opacity: 0.7, width: "80%" }} />
          </div>)
        })}
        <div style={{ display: "flex", justifyContent: "center" }}>
          <Button component={Link}
            to="/news" style={{ background: "transparent", border: "1px solid white", borderRadius: 20, color: "white", fontWeight: 500, marginTop: 15, marginBottom: 20 }}>VIEW MORE</Button></div>
      </div>)
    } else {
      return (<Typography style={{ fontSize: 16, fontWeight: 600, marginTop: 50, marginBottom: 100 }}>There is no news yet!</Typography>)
    }
  }

  return (

    <Container maxWidth="md" style={{ padding: 0 }}>
      <img alt="maskhead" src="/maskhead.png" style={{ width: "100%", opacity: 0.78 }} />
      <div className={classes.readMore}>
        <Button component={Link}
          to="/about" variant="contained" color="secondary" size="large" className={classes.readMore_Button}>Read More</Button>
      </div>
      <Grid container spacing={0} style={{ marginTop: -5 }}>
        <Grid item xs={12} sm={12} md={6} lg={6} xl={6} style={{ background: "#49b159", color: "white", padding: 10 }}>
          <Typography variant="h5" style={{ marginTop: 15, fontWeight: 600 }}>MOM NEWS</Typography>
          <Divider style={{ width: 120, marginTop: 8, marginBottom: 8, border: "1px solid white" }} />
          {renderNews()}


        </Grid>
        <Grid item xs={12} sm={12} md={6} lg={6} xl={6} style={{ background: "#fff", paddingLeft: 20, paddingRight: 20 }}>
          <Typography variant="h5" gutterBottom style={{ fontWeight: 700, marginBottom: 10, marginTop: 20 }} color="primary">
            Mission Organic Mizoram
          </Typography>

          <Typography gutterBottom style={{ marginBottom: 10 }}>
            Realizing the potential and benefits of organic farming and to improve the economic condition of farmers in the country, Government of India is promoting organic farming through the dedicated scheme called Mission Organic Value Chain Development for North Eastern Region (MOVCDNER) under National Mission for Sustainable Agriculture (NMSA) since 2015-16.
          </Typography>
          <Typography gutterBottom style={{ marginBottom: 10 }}>
            Under MOVCDNER, assistance is provided to the farmers in a value chain mode starting from formation of Farmers Producer Organizations (FPOs), on/off farm input production, supply of seeds/ planting materials, post-harvest infrastructure including collection, sorting, grading facilities,   establishment of integrated processing unit, refrigerated transportation, pre-cooling/ cold storage chamber, branding, labeling and packaging, etc.
          </Typography>
          <Typography gutterBottom>
            This scheme is implemented through State Governments at district and village level depending on the interest of the farmers. MOVCDNER scheme is implemented in the States of Arunachal Pradesh, Assam, Manipur, Meghalaya, Mizoram, Nagaland, Sikkim and Tripura since 2015-16.
          </Typography>
        </Grid>
      </Grid>
    </Container>


  );
};

export default Home;
